import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { ProgramPageViewModel } from "../../Model/ViewModel/ProgramPageViewModel";

/**
 * Generated class for the ProgramDetailModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-ionic-program-detail-add-edit-modal",
  templateUrl: "ionic-program-detail-add-edit-modal.html"
})
export class IonicProgramDetailAddEditModalPage {
  item: any;
  mode: string;
  CanEditPermission:boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {

    this.item = new ProgramPageViewModel();
    this.item.page_name=navParams.data.item.page_name;
    this.item.page_desc=navParams.data.item.page_desc;
    this.item.program_no=navParams.data.item.program_no;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.mode = navParams.data.mode;
    this.CanEditPermission=navParams.data.CanEditPermission;
  }

  Save() {

    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad IonicProgramDetailModalPage");
  }
}



